.App {
  text-align: center;
}

html, body, .root, .App {
  font-family: "Raleway", sans-serif;
  height: 100% !important;
}

*:focus {outline:none !important}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.homeNames {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 64px;
  letter-spacing: 4px;
  margin: 10px 0;
  line-height: 1.5;
}

.homeText {
  font-family: "Raleway", sans-serif;
  letter-spacing: 4px;
  font-weight: 100;
  margin: 10px 0;
  font-size: 30px;
  line-height: 1.5;
}

b {
  /*font-family: "Raleway", sans-serif;
  letter-spacing: 4px;
  margin: 10px 0;
  line-height: 1.5;*/
  font-weight: bold;
}

.startbgImage {
  background-image: url("assets/start2.jpeg");
  background-position: center;
  background-size: cover;
}

.countdownbgImage {
  position: relative;
  background-image: url("assets/flowers2.jpg");
  background-position: center;
  background-size: cover;
  padding-top: 250px;
  padding-bottom: 250px;
  color: #fff!important;
}

.invitedbgImage {
  position: relative;
  background-image: url("assets/blommor.jpg");
  background-position: center;
  background-size: cover;
  padding-top: 350px;
  padding-bottom: 350px;
  color: #fff!important;
}

.invitedbgImage > h1 {
  margin: 0px;
  font-size: 60px;
}

.countdownText {
  padding-top: 250px;
  padding-bottom: 250px;
  color: #fff!important;
}

.countdownText > h1 {
  font-size: 60px;
}

.navbar-nav {
  width: 100%;
  text-align: center;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
}

.navbar {
  opacity: 0.5;
  margin:0px;
}

.infoWrapper {
  background-color: #ffdddd !important;
}

.infoWrapper > h1 {
  margin: 0px;
  padding-top: 110px;
  padding-bottom: 50px;
}

.osaWrapper {
  background-color: #f2e1e1 !important;
}

.osaWrapper > h1 {
  margin: 0px;
  padding-top: 110px;
  padding-bottom: 50px;
}

.brollopWrapper {
  background-color: #ffdddd !important;
}

.brollopWrapper > h1 {
  margin: 0px;
  padding-top: 300px;
  padding-bottom: 50px;
}

.boendeWrapper {
  background-color: #ffdddd !important;
}

.boendeWrapper > h1 {
  margin: 0px;
  padding-top: 350px;
  padding-bottom: 350px;
}

.icons {
  width: 80px;
  height: 80px;
}

.infoOverText {
  padding-left: 100px;
  padding-right: 100px;
  font-size: 18px;
}

.modalButton {
  padding-bottom: 100px;
}

.navbarLinks {
  all: inherit;
  padding: 0px;
}

.navbarLinks:hover {
  text-decoration: none;
  color: #333333;
}

