.brollopInfo {
  margin: 0px;
  padding-bottom: 350px;
}

.osaButton{
  background-color: #f44336 !important;
  border: none;
  width: 250px;
  height: 70px;
  opacity: 0.5;
}

.selfMadeNavigation {
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 9999;
  box-sizing: inherit;
  background-color: #f8f8f8;
  opacity: 0.5;
}

.seldMadeLink {
  color: #777;
  width: 25%;
  float: left;
  padding: 8px 16px;
}

@media only screen and (max-width: 749px) {
  .infoGrid {
    display: grid;
    grid-template-rows: auto auto auto;
    grid-row-gap: 0px;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 20%;
    padding-right: 20%;
    font-size: 18px;
  }
  .innerInfoGrid {
    display: initial;
  }
  .iconGrid {
    display: grid;
    grid-template-rows: 80px auto;
    grid-row-gap: 0px;
    padding-bottom: 40px;
    align-items: center;
    justify-items: center;
  }
}

@media only screen and (min-width: 750px) {
  .infoGrid {
    display: grid;
    grid-template-rows: auto auto auto;
    grid-row-gap: 50px;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 20%;
    padding-right: 20%;
    font-size: 18px;
  }
  .innerInfoGrid {
    display: grid;
    grid-template-columns: 50% 50%;
  }
  .iconGrid {
    display: grid;
    grid-template-columns: 80px auto;
    align-items: center;
    justify-items: center;
  }
}